@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400&family=Roboto:wght@400;500;700&display=swap);
.todoList__userName{margin-bottom:5px;font-size:18px;font-weight:400}

.todoList__info{display:flex;justify-content:space-between;align-items:center;margin-bottom:8px}.todoList__title{font-weight:500}.todoList__status{font-size:20px;font-weight:400;color:red}.todoList__status__completed{color:#00cf00}

.todoList{list-style:none;color:#333}.todoList__item{margin-bottom:5px;padding:10px;border:1px solid #616060;border-radius:8px;background-color:rgba(223,223,223,0.637)}

body,ul,h1,h2,h3{margin:0;padding:0}html{font-family:'Roboto', 'Helvetica', sans-serif}.App{margin:0 auto;width:-webkit-max-content;width:max-content}.App__title{margin-bottom:20px;text-align:center;color:#333}

